@mixin phone-reset {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        float: none;
        width: auto;
    }
}
@mixin phone-center {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        text-align: center;
    }
}
@mixin phone-padder {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        padding: 0;
    }
}
@mixin phone-padder-add {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
            padding: 2em 1em;
    }
}
@mixin phone-margin {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        margin: 0 0 1em;
    }
}
@mixin phone-hide {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        display: none;
    }
}
@mixin phone-table {
    /* Smartphones (portrait and landscape) ----------- */
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        overflow-x: auto;
        display: block;
    }
}
@import '_icons.scss';
@import '_baguetteBox.scss';

$link: #503b75;
$light-grey: #f1f1f1;
$medium-grey: #e0e0e0;
$main-color: #000;

* {
    margin: 0;
    padding: 0;
}

body,
html {
    background: #fff;
    color: #777;
    font-family: 'Raleway', sans-serif;
    height: 100%;
    @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
        height: auto;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin-bottom: 1em;
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    color: #000;
}

h1 {
    font-size: 2.4em;
}

h2 {
    font-size: 2.2em;
}

h3 {
    font-size: 1.8em;
}

h4 {
    font-size: 1.2em;
}

p {
    line-height: 1.8em;
    margin-bottom: 1.4em;
    max-width: 700px;
    font-weight: 400;
    font-size: 1.1em;
}

p:empty {
    display: none;
}

a,
a:link {
    color: $link;
    text-decoration: none;
    transition: 0.222s;
    font-weight: 600;

    &:hover {
        color: lighten($link, 10%);
    }
}

ol,
ul {
    list-style: none;
    margin: 0;
}

.clearfix {
    *zoom: 1;

    &:after,
    &:before {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}

b,
strong {
    font-weight: 700;
    color: #333;
}

.hidden {
    display: none;
}

.button {
    display: inline-block;

    .button-inner {
        background: lighten($main-color, 10%);
        display: block;
        padding: 0.4em 2em;
        border-radius: 2px;
        transition: 0.222s;
        color: $light-grey;

        .lnr {
            margin-right: 0.4em;
        }
    }

    &:hover {
        .button-inner {
            background: lighten($main-color, 15%);
            color: #fff;
        }
    }
}

.button-download {
    display: inline-block;

    .button-inner {
        background: $light-grey;
        display: block;
        padding: 0.4em 2em;
        border-radius: 2px;
        transition: 0.222s;
        color: #777;
    }

    &:hover {
        .button-inner {
            color: #000;
            //background: darken($main-color, 10%);
        }
    }
}

.padder {
    padding: 4em 0;
    @media only screen and (max-width : 1100px) {
        padding: 2em 1em;
    }
}

.center {
    text-align: center;
}

.wrapper {
    width: 1100px;
    margin: 0 auto;
    @include phone-reset;
}

table {
    width: 100%;
    margin-bottom: 1em;
    border: none;
    border-collapse: collapse;

    tr {
        th {
            color: $main-color;
            padding: 0.4em 0;
            border-bottom: solid 3px $light-grey;
        }

        td {
            border: none;
            border-bottom: solid 1px $light-grey;
            padding: 0.4em 0;
        }
    }

    &:last-child {
        margin-bottom: 0;
    }
}

pre {
    margin-bottom: 2em;
    display: block;

    code {
        overflow-x: scroll;
        max-width: 100%;
        display: inline-block;
        border-radius: 2px;
        background: $light-grey;
        border: solid 1px $medium-grey;
        padding: 1em;
        font-size: 1.1em;
    }
}

header {
    .padder {
        p {
            opacity: 0.8;

            &:last-child {
                margin: 0;
            }
        }
    }
}

footer {
    background: #f1f1f1;
    text-align: center;
    .padder {
        padding: 2em 0;

        p {
            max-width: none;
            margin: 0;
        }
    }
}

.work {
    background: #f1f1f1;

    p {
        margin: 0;
    }
}

.align-center {
    text-align: center;
    p {
      margin: 0 auto 1em;
    }
}

.entry-content {
    width: 700px;
    @include phone-reset;
    margin: 0 auto;

    ul {
        font-size: 1.1em;
        list-style: circle;
        margin: 0 0 1.4em 2em;
        line-height: 1.8em;
        max-width: 700px;

        li {
            margin-bottom: 0.4em;

            &:last-child {
                margin: 0;
            }
        }
    }
}

.grapecity {
    .wrapper {
        background: #392a53;
        margin: 0;

        .padder {
            padding: 4em;
            @include phone-padder-add;
        }
    }

    h3,
    p {
        color: #fff;
    }

    p {
        opacity: 0.8;
        margin: 0;
        max-width: none;
    }
}

.solutions {
    background: #f1f1f1;
    .body-img {
        width: 49%;
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        @include phone-reset;
        img {
          border: solid 1px #f1f1f1;
          border-radius: 2px;
          box-shadow: 0 0 50px rgba(0,0,0,0.10);
          max-width: 100%;
          @include phone-margin;
        }
    }

    .body-text {
        width: 50%;
        display: inline-block;
        vertical-align: middle;
        @include phone-reset;
        .padder {
          @include phone-padder;
        }
    }
}

.product-screenshot {
  img {
    max-width: 100%;
    box-shadow: 0 0 80px rgba(0,0,0,0.10);
  }
}

.intro-section {
    .padder {
        padding: 2em 2em 0;

        h2 {
            font-size: 3.4em;
            max-width: 700px;
            margin: 0 auto 1em;
            position: relative;
            z-index: 2;
            @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
              font-size: 2em;
            }
        }

        img {
            max-width: 100%;
            margin-bottom: -200px;
            @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
              margin: 0;
            }
        }
    }
}

.cards {
    margin: 0;

    li {
        float: left;
        text-align: center;
        @include phone-reset;

        &:before {
            display: none;
        }

        .card-container {
            border: solid 1px $medium-grey;
            border-radius: 2px;
            overflow: hidden;
            margin: 0 0.5em;
            position: relative;
            @include phone-margin;

            .card-header {
                background-color: $light-grey;
                background-size: cover;
                position: relative;
                background-position: center center;
                border-radius: 0;
                border-top-left-radius: 2px;
                border-top-right-radius: 2px;
                border: none;
                margin: 0;

                .padder {
                    padding: 2em 1em;

                    .lnr,
                    i,
                    img {
                        height: 82px;
                        width: 90px;
                        position: relative;
                        z-index: 2;
                        color: #fff;
                        font-size: 86px;
                    }

                    img {
                        //border-radius: 90px;
                    }
                }
            }

            .default {
                background-color: $light-grey;
                border-bottom: solid 1px $light-grey;

                &:after {
                    background-color: $light-grey;
                    display: none;
                }

                &:before {
                    border-color: transparent transparent transparent $light-grey;
                    display: none;
                }
            }

            .card-body {
                .padder {
                    padding: 2em 1em 6em;

                    .button {
                        position: absolute;
                        bottom: 2em;
                        left: 50%;
                        width: 80%;
                        margin-left: -40%;

                        .button-inner {
                            padding: 1em;
                        }
                    }

                    ul {
                        margin: 0 0 1em;

                        li {
                            float: none;
                            width: auto;

                            &:before {
                                display: none;
                            }
                        }
                    }
                }
            }
        }

        .card-highlight {
            box-shadow: 0 0 30px rgba(0,0,0,0.20);
            border: none;
            position: relative;
            z-index: 2;
        }

        &:nth-child(3n+1) {
            .card-container {
                margin-right: 1em;
                margin-left: 0;
                @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
                    margin-right: 0;
                }
            }
        }

        &:nth-child(3n) {
            .card-container {
                margin-left: 1em;
                margin-right: 0;
                @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
                    margin-left: 0;
                }
            }
        }

        &:nth-child(n+4) {
            .card-container {
                margin-top: 1.5em;
            }
        }
    }

    &.items-3 {
        li {
            width: 33.333%;
        }
    }
}

.column-icons {
    margin-bottom: 2em;

    li {
        float: left;
        width: 33.333%;
        text-align: center;
        @include phone-reset;

        .padder {
            padding: 2em 0;
            @include phone-padder;
            @include phone-margin;

            h3 {
                margin: 0;
            }

            p {
                padding: 0 1em;
            }

            .lnr {
                display: inline-block;
                font-size: 64px;
                margin-bottom: 6px;
            }
        }

        .column-inner {
            display: block;
            background: #fff;
            border: solid 1px $medium-grey;
            padding: 2em;
            margin: 0 0.5em;
            transition: 0.222s;
            border-radius: 2px;
            @include phone-margin;

            &:hover {
                box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
                border-color: $medium-grey;
            }

            .column-bg {
                height: 100px;
                background-repeat: no-repeat;
                background-position: center center;
                margin-bottom: 1em;
            }

            h3 {
                color: $link;
                margin-bottom: 0.4em;

                .status {
                    background: $main-color;
                    font-size: 0.6em;
                    padding: 0.4em 1em;
                    position: relative;
                    top: -1px;
                    margin-left: 0.4em;
                    border-radius: 24px;
                    text-transform: uppercase;
                    display: inline-block;
                    vertical-align: middle;
                    color: $light-grey;
                }
            }

            p {
                font-weight: 400;
                padding: 0;
                margin: 0;
                color: #777;
            }

            .lnr {
                display: inline-block;
                font-size: 64px;
                margin-bottom: 6px;
            }

            .button {
                margin-top: 1em;

                .button-inner {
                    .lnr {
                        font-size: 1em;
                    }
                }
            }
        }
    }

    &.items-3 {
        li {
            &:nth-child(3n+1) {
                .column-inner {
                    margin: 0 1em 0 0;
                    @include phone-margin;
                }
            }

            &:nth-child(3n) {
                .column-inner {
                    margin: 0 0 0 1em;
                    @include phone-margin;
                }
            }

            &:nth-child(n+4) {
                .column-inner {
                    margin-top: 1.5em;
                    @include phone-margin;
                }
            }
        }
    }

    &.items-2 {
        li {
            &:nth-child(2n+1) {
                .column-inner {
                    margin: 0 0.75em 0 0;
                    @include phone-margin;
                }
            }

            &:nth-child(2n) {
                .column-inner {
                    margin: 0 0 0 0.75em;
                    @include phone-margin;
                }
            }

            &:nth-child(n+3) {
                .column-inner {
                    margin-top: 1.5em;
                    @include phone-margin;
                }
            }
        }
    }
}

.value-icons {
    li {
        float: left;
        width: 20%;
        text-align: center;
        @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
            width: 50%;
        }

        .padder {
            padding: 0;
            margin-top: 2em;
            @include phone-margin;

            .lnr,
            i {
                background: $light-grey;
                color: $main-color;
                border-radius: 2px;
                padding: 0.4em;
                line-height: 64px;
                display: inline-block;
                font-size: 64px;
                margin-bottom: 12px;
            }

            h4 {
                margin: 0;
            }

            img {
                max-height: 60px;
                width: auto;
            }
        }

        &:nth-child(n+6) {
            .padder {}
        }
    }
}

.highlight {
    display: table;

    .highlight-text {
        display: table-cell;
        vertical-align: middle;
        width: 50%;
        @include phone-reset;
        @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
            display: block;
        }

        p {
            margin-bottom: 1em;
        }

        .button {
            margin-bottom: 1em;

            .button-inner {
                background: $light-grey;
                color: $main-color;
            }

            &:hover {
                .button-inner {
                    background: $medium-grey;
                    color: #000;
                }
            }
        }

        a {
            font-weight: 700;
        }
    }

    .highlight-img {
        display: table-cell;
        vertical-align: middle;
        width: 50%;
        @include phone-reset;
        @media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
            display: block;
            margin-top: 2em;
        }

        .padder {
            padding: 0 0 0 2em;
            text-align: center;
            @include phone-padder;

            img {
                max-width: 100%;
                border-radius: 2px;
                border: solid 1px $light-grey;
                box-shadow: 0 0 50px rgba(0,0,0,0.10);
            }
        }
    }
}

.gallery {
  background: $light-grey;
  .portfolio {
    a {
      display: block;
      float: left;
      width: 12.5%;
      @include phone-reset;
      .portfolio-inner {
        border: solid 5px #fff;
        box-shadow: 0 0 20px rgba(0,0,0,0.20);
        margin: 1em;
        @include phone-margin;
      }
      img {
        width: 100%;
        height: auto;
        margin-bottom: -4px;
      }
    }
  }
}
